<template>
  <div>
      <!--====== PRELOADER PART START ======-->

    <!-- <div class="loader-wrap">
        <div class="preloader"><div class="preloader-close">Preloader Close</div></div>
        <div class="layer layer-one"><span class="overlay"></span></div>
        <div class="layer layer-two"><span class="overlay"></span></div>        
        <div class="layer layer-three"><span class="overlay"></span></div>        
    </div> -->

    <!--====== PRELOADER PART ENDS ======-->

    <!--====== OFFCANVAS MENU PART START ======-->

    <sidebar-home-one :sidebar="sidebar" @toggleSidebar="toggleSidebar"  :menuItems="navs" />

    <!--====== OFFCANVAS MENU PART ENDS ======-->
   
    <!--====== PART START ======-->
    
    <header-blogs @toggleSidebar="toggleSidebar" :menuItems="navs"/>

    <!--====== PART ENDS ======-->

    <!--====== APPIE PAGE TITLE PART START ======-->
    
    <title-area/>
    
    <!--====== APPIE PAGE TITLE PART ENDS ======-->

        <!-- Blog Start -->
        <section class="blogpage-section">
            <div class="container">
                <div class="row">
                    <div class="col-lg-8 col-md-7">
                        <all-blogs/>
                    </div>
                    <div class="col-lg-4 col-md-5">
                        <blog-sidebar/>
                    </div>
                </div>
            </div>
        </section>
        <!-- Blog End -->

    <!--====== APPIE FOOTER PART START ======-->
    
    <footer-home-one/>
    
    <!--====== APPIE FOOTER PART ENDS ======-->


    <!--====== APPIE BACK TO TOP PART ENDS ======-->
    <div class="back-to-top">
        <a href="#"><i class="fal fa-arrow-up"></i></a>
    </div>
    <!--====== APPIE BACK TO TOP PART ENDS ======-->
  </div>
</template>

<script>
import SidebarHomeOne from '../HomeOne/SidebarHomeOne.vue'
import HeaderBlogs from '../Blogs/HeaderBlogs.vue'
import TitleArea from '../Blogs/TitleArea.vue'
import FooterHomeOne from '../HomeOne/FooterHomeOne.vue'
import AllBlogs from '../Blogs/AllBlogs.vue'
import BlogSidebar from '../BlogSidebar.vue'
export default {
	components: { SidebarHomeOne, HeaderBlogs, TitleArea, FooterHomeOne, AllBlogs, BlogSidebar },
        data(){
        return{
            sidebar:false,
              navs:[
            {
                name:'home',
                path:'/'
            },
            {
                name:'About us',
                path:'/about-us'
            },
            {
                name:'services',
                path:'/services'
            },
                {
                name:'pages',
                    childrens:[
                    {
                        name:'Error',
                        path:'/error'
                    }
                ],
            },
            {
                name:'blogs',
                    childrens:[
                    {
                        name:'Blogs',
                        path:'/blogs'
                    },
                    {
                        name:'Blog Details',
                        path:'/blog-details'
                    },
                ],
            },
            {
                name:'contact',
                    path:'/contact'
            }
            ]
        }
    },
     mounted() {
        document.addEventListener('scroll', this.topToBottom)
    },
    methods:{
        topToBottom(){
        const result = document.querySelector('.back-to-top')
           if (document.body.scrollTop > window.innerHeight || document.documentElement.scrollTop > window.innerHeight ) {
                result.style.display = "block";
            } else {
                result.style.display = "none";
            }
        },
         toggleSidebar(){
           this.sidebar = !this.sidebar
        },  
    }

}
</script>

